#hue-bar {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(to right, red, #ffff00, green, #00ffff, blue, #ff00ff, red);
}

#hue-selector {
  position: relative;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  box-shadow: 0 0 0 3pt black;
  border-width: 1px;
  border-style: solid;
  cursor: grab;
}

#sv-box {
  position: relative;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

#sv-selector {
  position: relative;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  box-shadow: 0 0 0 3pt black;
  border-width: 1px;
  border-style: solid;
  cursor: grab;
}

.radiocontainer {
  margin-top: 0.5rem;
}

.tool .tooltiptext {
  visibility: hidden;
  margin-left: 0;
  margin-top: -30px;
  width: max-content;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 5px;
  transform: translateX(-50%);
  z-index: 1;
  border-radius: 5px;
}

.tool:hover .tooltiptext {
  visibility: visible;
}

.tool>*:not(.tooltiptext) {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
}

.toolBarButton {
    width: 50%;
    padding: 0px;
}
