label.radiocontainer {
  cursor: pointer;
}

.radiocontainer input[type=radio]:checked~span {
  background-color: rgba(0, 0, 0, 0.3);
}

.radiocontainer input[type=radio] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.radiocontainer input[type=radio]~span {
  padding: 10px 20px;
  user-select: none;
}

label.checkboxcontainer {
  cursor: pointer;
}

.checkboxcontainer input[type=checkbox]:checked~span {
  background-color: rgba(0, 0, 0, 0.3);
}

.checkboxcontainer input[type=checkbox] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkboxcontainer input[type=checkbox]~span {
  padding: 10px 20px;
  user-select: none;
}

